 .ui-success-path {
	 stroke-dasharray: 60px 64px;
	 stroke-dashoffset: 62px;
	 stroke-linecap: round;
	 -webkit-animation: ani-success-path .4s 1s ease-in both;
	         animation: ani-success-path .4s 1s ease-in both;
}
